<template>
  <div class="bindSettings">
    <div class="list" v-if="this.$route.query.isshow==0">
      <div class="item flex-center-y img1">
        <div class="img">绑定手机号码</div>
        <input type="text" placeholder="请输入新的手机号码" v-model="data.mobile" />
      </div>
      <!--       <div class="item flex-center-y img1">
        <div>
          <img src />
        </div>
        <input type="text" placeholder="请输入短信验证码" v-model="data.code" />
        <div
          class="code flex-center"
          :class="codeText !== '获取验证码' ? 'gary' : ''"
          @click="getCode"
        >{{codeText}}</div>
      </div>-->
      <div class="button flex-center" @click="submit">绑定</div>
    </div>
    <div class="list" v-if="this.$route.query.isshow==1">
      <div class="item flex-center-y img2">
        <div class="img">真实姓名</div>
        <input type="text" placeholder="请输入您的真实姓名" v-model="data.name" />
      </div>
      <div class="item flex-center-y img2">
        <div class="img">身份证号码</div>
        <input type="text" placeholder="请输入您的身份证号码" v-model="data.code" />
      </div>
      <div class="item flex-center-y img2">
        <div class="img">详细地址</div>
        <input type="text" placeholder="请输入您的详细地址" v-model="data.address" />
      </div>
      <div class="button flex-center" @click="submit_shiming">提交</div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import url from "@/axios/api.js";
import { Toast } from "vant";
export default {
  data() {
    return {
      data: {
        name: "",
        code: "",
        mobile: "",
        address: ""
      },
      codeText: "获取验证码",
      code: ""
    };
  },
  methods: {
    getCode() {
      if (!/^1[3456789]\d{9}$/.test(this.data.mobile)) {
        Toast("请输入正确的手机号码");
        return false;
      } else if (this.codeText !== "获取验证码") {
        return false;
      }
      smsSend({ mobile: this.data.mobile }).then(res => {
        Toast(res.data.msg);
      });
      let index = 60;
      let clock = window.setInterval(() => {
        this.codeText = index + "s";
        index--;
        if (index < 0) {
          window.clearInterval(clock);
          this.codeText = "获取验证码";
        }
      }, 1000);
    },
    submit() {
      // if(this.data.code&&this.data.mobile){
      let params = new URLSearchParams();

      params.append("openid", localStorage.getItem("openid"));
      params.append("mobile", this.data.mobile);
      axios.post(url.bindPhone, params).then(res => {
        Toast(res.data.data.msg);
        if (res.code == 200) {
          Toast("绑定成功");
          this.$router.go(-1);
        }
      });
      // }else if(!(/^1[3456789]\d{9}$/.test(this.data.mobile))){
      //   Toast("请填写正确的手机号码");
      // }else{
      //   Toast("请填写验证码")
      // }
    },
    submit_shiming() {
      if (this.data.code && this.data.name) {
        let params = new URLSearchParams();

        params.append("openid", localStorage.getItem("openid"));
        params.append("realname", this.data.name);
        params.append("idcard", this.data.code);
        params.append("address", this.data.address);
        axios.post(url.bindName, params).then(res => {
          Toast("数据已提交");
          this.$router.go(-1);
        });
      } else {
        Toast("请填写信息");
      }
    }
  }
};
</script>

<style scoped lang="scss">
.list {
  padding: 10px;
}
.item {
  display: flex;
  border-bottom: 1px solid #ebebeb;
  font-size: 16px;
  padding: 20px 0;
  img {
    width: 36px;
    margin-right: 20px;
  }
  .img1 {
    input {
      margin-left: auto;
      padding: 0 15pt;
      &::-webkit-input-placeholder {
        padding-left: 30pt;
        color: #b7b7bb;
      }
    }
  }
  input {
    background: none;
    border: none;
    flex: 1;
    padding: 0 15pt;
    &::-webkit-input-placeholder {
      // text-align: right;
      color: #b7b7bb;
    }
  }
  .code {
    width: 170px;
    height: 50px;
    background: #ff4c23;
    font-size: 16px;
    border-radius: 60px;
    color: #fff;
  }
  .gary {
    background: #c6c6c6;
  }
}
.button {
  margin: 50px auto;
  background: linear-gradient(to right, #ff462d, #ff9e3f);
  color: #fff;
  border-radius: 60px;
  font-size: 16px;
  padding: 20px 0;
  text-align: center;
}
.img2 {
  input {
    background: none;
    border: none;
    // flex: 1;
    // padding:0 15pt;
    &::-webkit-input-placeholder {
      text-align: right;
      color: #b7b7bb;
    }
  }
}
</style>


